:root {
    --bs-border-color: var(--tc-system-stroke) !important;
    --bs-primary-rgb: 255, 101, 46;
    --bs-border-radius-xxl: 20px;

    .border-primary-2 {
        --bs-border-color: var(--tc-system-navy-blue-2);
    }

    .bg-dark-3 {
        background-color: #2d2d2d;
    }

    .bg-primary-2 {
        background-color: var(--tc-system-navy-blue-2);
    }

    .text-primary-2 {
        color: var(--tc-system-navy-blue-2);
    }
}

// background colors
.bg-light {
    background-color: var(--tc-bg-light) !important;
}

.bg-dark-5 {
    background-color: #24222c;
}

// text colors
.text-dark {
    color: var(--tc-system-black) !important;
}

.text-stroke {
    color: var(--tc-system-stroke);
}

.btn-text {
    font-family: var(--tc-btn-font-family);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
    /* 14px */
    text-transform: uppercase;
}

.content-top {
    .line {
        width: 20px;
        height: 1px;
        opacity: 0.5;
        background-color: var(--tc-theme-primary);
    }
}

.fill-primary {
    path {
        fill: var(--tc-theme-primary) !important;
    }

    fill: var(--tc-theme-primary) !important;
}

// variables end
.filter-invert {
    filter: invert(1);
}

.contact-map {
    max-height: 700px;

    iframe {
        height: 700px;
        width: 100%;
    }
}

.map {
    -webkit-filter: grayscale(75%);
    -moz-filter: grayscale(75%);
    -ms-filter: grayscale(75%);
    -o-filter: grayscale(75%);
    filter: grayscale(75%);
}
